export default {
	data() {
		return {
			mustReadList: []
		}
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		
		uni.request({
			//TODO 替换成查询文章列表的url
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data);
				this.mustReadList = res.data;
				uni.hideLoading();
			},
			fail: () => {
				console.log("获取列表失败！");
				uni.showToast({
				    icon: "none", 
					title: '获取列表失败！',
				    duration: 2000
				});
				uni.hideToast();
				uni.hideLoading();
			},
			complete: () => {}
		});
	},
	methods: {
		openinfo(e){
			var article_id = e.currentTarget.dataset.article_id;
			uni.navigateTo({
				url: '/pages/article-info/article-info?article_id='+article_id,
			});
		}
	}
}
